<template>
  <div>
    <headerBar :header-data="headerData" />
    <div v-if="headerData.title==='物流信息'" class="container">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title-sm">
            <div class="title-left">
              <h2>快递信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="page-list">
            <div class="pagelist-left">物流公司</div>
            <div class="pagelist-right left">{{ expresses.name }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">物流单号</div>
            <div class="pagelist-right left">{{ expresses.num }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">备注</div>
            <div class="pagelist-right left">{{ beiz }}</div>
          </div>

          <!-- <van-steps direction="vertical" :active="0">
            <van-step>
              <h3>【城市】物流状态1</h3>
              <p>2016-07-12 12:40</p>
            </van-step>
            <van-step>
              <h3>【城市】物流状态2</h3>
              <p>2016-07-11 10:00</p>
            </van-step>
            <van-step>
              <h3>快件已发货</h3>
              <p>2016-07-10 09:30</p>
            </van-step>
          </van-steps> -->

        </div>
      </div>
    </div>

    <div v-if="headerData.title==='操作日志'" class="container">
      <div class="content">
        <div class="common-main box">
          <!-- <div class="title-sm">
            <div class="title-left">
              <h2>操作日志</h2>
            </div>
          </div> -->
          <div class="overviews-timeline active">
            <div class="vertical-container-sprz">
              <div class="vertical-timeline-block lcsp-done">
                <div class="vertical-timeline-icon" />
                <div class="vertical-timeline-content">
                  <h4>开始</h4>
                </div>
              </div>
              <div v-for="(orderLog,index) in OrderLogList" :key="index" class="vertical-timeline-block lcsp-done">
                <div class="vertical-timeline-icon">
                  <i class="iconfont iconduihao" />
                </div>
                <div class="vertical-timeline-content">
                  <h4>{{ orderLog.content }}</h4>
                  <div class="vertical-timeline-hide">
                    <p>
                      <span class="sprz-name">{{ orderLog.czymc }}</span>
                    </p>
                    <p>
                      <span class="sprz-date"><em>操作时间</em> {{ orderLog.CREATE_TIME_FORMAT }} </span>
                    </p>
                  </div>
                </div>
              </div>
              <div class="vertical-timeline-block lcsp-done">
                <div class="vertical-timeline-icon" />
                <div class="vertical-timeline-content">
                  <h4>结束</h4>
                </div>
              </div>
            </div>
          </div>
          <!-- <van-steps v-for="(orderLog,index) of OrderLogList" :key="index" direction="vertical" :active="0">
            <van-step>
              <h3>{{ orderLog.czymc }}-{{ orderLog.content }}</h3>
              <p>{{ orderLog.CREATE_TIME_FORMAT }}</p>
            </van-step>
          </van-steps> -->

        </div>
      </div>
    </div>

  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { listOrderLog, orderExpress } from '@/store/module.js'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'OrderExpress',
  components: {
    headerBar
  },
  // mixins: [pageRouter],
  data() {
    return {
      checked: true,
      OrderExpress: '',
      activities: [],
      expresses: '',
      orderId: '',
      OrderLogList: '',
      headerData: {
        title: '订单跟踪',
        leftUrl: 'back',
        leftText: ' '
      },
      beiz: ''
    }
  },
  created() {
    this.headerData.title = this.$route.params.title
    this.beiz = this.$route.params.beiz
    this.orderId = this.$store.state.member.orderId
    if (this.headerData.title === '物流信息') {
      this.getOrderExpressLogAction()
    } else {
      this.getOrderLogAction()
    }
  },
  methods: {
    getOrderLogAction() {
      listOrderLog.data = { e_order_id: this.orderId }
      this.asyncApiModule(listOrderLog).then(res => {
        this.OrderLogList = res
      })
        .catch(() => {
        })
    },
    goBack() {
      this.$router.back()
    },
    getOrderExpressLogAction() {
      orderExpress.data = { e_order_id: this.orderId }
      this.asyncApiModule(orderExpress).then(res => {
        this.activities = []
        this.expresses = res
        if (res.detail === '查询物流失败！') {
          this.activities = []
        } else {
          for (let index = 0; index < res.detail.length; index++) {
            let log = {}
            if (index === res.detail.length) {
              log = { content: res.detail[index].content,
                timestamp: res.detail[index].msgTime,
                size: 'large',
                type: 'primary',
                icon: 'el-icon-check',
                color: '#52C41A'
              }
            } else {
              log = { content: res.detail[index].content,
                timestamp: res.detail[index].msgTime,
                size: 'normal' }
            }

            this.activities.push(log)
          }
        }
      })
        .catch(() => {
        })
    },
    goAddressadd() {
      this.$router.push({
        name: 'AddressAdd'
      })
    }
  }
}
</script>

<style lang="css" scoped>

</style>
